input[type=text][data-v-c0d2ce64], select[data-v-c0d2ce64], textarea[data-v-c0d2ce64]{
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  resize: vertical;
}

/* Style the label to display next to the inputs */
label[data-v-c0d2ce64] {
  display: inline-block;
}

/* Style the submit button */
.mortgage_submit[data-v-c0d2ce64] {
  background-color: #f95405;
  color: white;
  padding: 15px 120px 21px 120px;;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
}

/* Style the container */
.mortgage_calculator[data-v-c0d2ce64], .mortgage_calculated[data-v-c0d2ce64] {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
  width: 100%
}

/* Floating column for labels: 25% width */
.col-25[data-v-c0d2ce64] {
  float: left;
  width: 50%;
  margin-top: 6px;
}

/* Floating column for inputs: 75% width */
.col-75[data-v-c0d2ce64] {
  float: left;
  width: 50%;
  margin-top: 6px;
}

/* Clear floats after the columns */
.row[data-v-c0d2ce64]:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
.col-25[data-v-c0d2ce64], .col-75[data-v-c0d2ce64], .mortgage_submit[data-v-c0d2ce64] {
    width: 100%;
    margin-top: 0;
}
}
.error[data-v-c0d2ce64] {
    color: rgb(255, 0, 0);
    text-align: center;
    font-weight: bold;
}
.heading[data-v-c0d2ce64] {
    color: #0ca1ac;
}




















